html {
  box-sizing: border-box;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
}

body {
  background-image: url("../public/background.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  font-family: "Lato", sans-serif;
  background-color: #f9f9f9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  overflow: hidden;
  background-attachment: fixed;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: 1fr;
  }

  form {
    flex-direction: column;
    align-items: stretch;
    width: 100%;
  }

  form input[type="text"] {
    margin-right: 0;
    margin-bottom: 10px;
    width: 100%;
  }

  form button {
    width: 100%;
  }

  ul {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  li {
    flex-direction: column;
    align-items: flex-start;
    padding: 15px;
    margin-bottom: 15px;
    width: 100%;
  }

  li .done,
  li .remove {
    width: 40px;
    height: 40px;
    font-size: 20px;
  }

  h1 {
    font-size: 1.5rem;
    text-align: center;
  }
}

@media (max-width: 480px) {
  body {
    background-size: contain;
  }

  h1 {
    font-size: 1.2rem;
  }

  li .task-content {
    font-size: 0.9rem;
  }

  form input[type="text"] {
    font-size: 0.9rem;
  }

  li .done,
  li .remove {
    width: 35px;
    height: 35px;
    font-size: 18px;
  }
}
